@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
}

.bold100 {
  font-weight: 100;
}

.bold200 {
  font-weight: 200;
}

.bold300 {
  font-weight: 300;
}

.bold400 {
  font-weight: 400;
}

.bold800 {
  font-weight: 800;
}

.bold {
  font-weight: bold;
}

.colorsaumon {
  color: #e47272;
}

.colorsaumongris {
  color: #ffcdcd;
}

.Prestation {
  background: #1277ba;
  display: flex;
  flex-direction: column;
  padding-bottom: 40px;
}

.Prestation .titlePrestation {
  color: #ffffff;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 71px;
  line-height: 97px;
  margin: 45px auto;
}

.Prestation .bigbloc {
  display: flex;
  grid-gap: 30px;
  gap: 30px;
  margin: 0px 30px 30px 30px;
  justify-content: center;
}

.containerSectionPrestation {
  padding: 10px 17px 0 38px;
  width: 644px;
  height: auto;
  padding-bottom: 20px;
  border: 3px solid rgba(255, 255, 255, 0.47);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  display: flex;
  align-items: center;
}

.containerSectionPrestation h2 {
  width: 256px;
}

.containerSectionPrestation p {
  line-height: 17px;
  width: 310px;
  text-align: justify;
  color: #000000;
}

.containerSectionPrestation button {
  margin-top: 30px;
  background-color: #0075ff;
  /* Green */
  border: none;
  color: white;
  padding: 11px 13px;
  text-align: center;
  font-size: 16px;
  box-shadow: 3px 5px 10px 1px rgba(0, 0, 0, 0.47);
  border-radius: 8px;
}

.containerSectionPrestation img {
  margin-left: auto;
  padding-left: 20px;
  width: auto;
  height: 170px;
}

.bloc1 {
  background: #76baef;
}

.bloc2 {
  background: #ffdadb;
}

.creation {
  background-color: #d7dfe4;
  padding-bottom: 50px;
  margin: -20px auto auto auto;
}

.creation h1 {
  width: 200px;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 71px;
  line-height: 97px;
  /* linear */
  background: linear-gradient(90deg, #184f6f 0%, rgba(255, 101, 0, 0.99) 100%);
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
}

.GridCreation {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 24px;
  gap: 24px;
  margin: 70px 180px;
}

.GridCreation div {
  box-sizing: border-box;
  width: 100%;
  height: 372px;
  background: #1e7ee2;
  background: #1e7ee2;
  border: 3px solid rgba(255, 255, 255, 0.47);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
}
